	module.exports = {"data":{"talkJson":{"description":"Habits help you manage the complexity of code. You apply existing skill and knowledge automatically to the detail while focusing on the bigger picture. But because you acquire habits largely by imitation, and rarely question them, how do you know your habits are effective? Many of the habits that programmers have for naming, formatting, commenting and unit testing do not stand up as rational and practical on closer inspection. Kevlin Henney examines seven coding habits that are not as effective as programmers believe, and to suggest alternatives.","meta":{"duration":"PT46M29S"},"presenter":{"name":"Kevlin Henney"},"slug":"seven-ineffective-coding-habits-of-many-programmers","source":{"id":"ZsHMHukIlJY","provider":"youtube"},"tag":["Software Architecture","Software Design"],"title":"Seven Ineffective Coding Habits of Many Programmers"},"allTalkJson":null},"pathContext":{"slug":"seven-ineffective-coding-habits-of-many-programmers","tags":["Software Architecture","Software Design"]}}

